html,
body {
  -webkit-font-smoothing: antialiased;
  background-color: transparent;
  background-color: transparent;
  font-size: 10px;
  color: #222222;
}

body,
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Oswald", sans-serif;
}

a {
  -webkit-tap-highlight-color: transparent;
  -webkit-tap-highlight-color: transparent;
  color: blue;
  outline: none;
}

a:active, a:hover {
  color: #3333ff;
}

img {
  -webkit-tap-highlight-color: transparent;
  -webkit-tap-highlight-color: transparent;
}

.clearfix:before,
.clearfix:after {
  content: " ";
  display: table;
}

.clearfix:after {
  clear: both;
}

.home .slider {
  position: relative;
  width: 100%;
  height: 350px;
}

@media (min-width: 640px) {
  .home .slider {
    height: 800px;
  }
}

.home .slider [class*='slide-'] {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #fff no-repeat center center;
  background-size: cover;
  z-index: 0;
  transition: opacity .4s linear;
  transition: z-index 0 linear 0.4s;
  opacity: 0;
}

.home .slider [class*='slide-'].active {
  opacity: 1;
  z-index: 1;
}

.home .slider [class*='slide-'] .mask {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.4);
}

.home .slider [class*='slide-'] .content-of-slide {
  width: 100%;
  padding-top: 20px;
  padding-bottom: 20px;
}

.home .slider [class*='slide-'] .content-of-slide h2 {
  margin: 0;
  font-size: 2em;
  text-transform: uppercase;
  color: #ffffff;
}

.home .slider [class*='slide-'] .content-of-slide p {
  margin: 5px 0;
  font-size: 1.2em;
  font-weight: 300;
  color: #ffffff;
}

.home .slider [class*='slide-'] .content-of-slide .read-more {
  display: none;
}

.home .slider [class*='slide-'] .content-of-slide .button {
  display: inline-block;
  margin: 10px 0;
  padding: 10px;
  border: 1px solid #ffffff;
  text-align: center;
  color: #ffffff;
  text-decoration: none;
}

.home .slider [class*='slide-'] .content-of-slide .button:hover {
  background: #ffffff;
  color: #000;
}

.home .slider .progress-of-slides {
  position: absolute;
  bottom: 20px;
  left: 0;
  z-index: 1;
  width: 100%;
}

.home .slider .progress-of-slides .progress-btn {
  font-size: 13px;
  text-transform: uppercase;
  width: 10px;
  height: 10px;
  background: #ffffff;
  color: #ffffff;
  float: left;
  border-radius: 5px;
  margin: 0 5px;
  overflow: hidden;
  cursor: pointer;
}

.home .slider .progress-of-slides .progress-btn:first-child {
  margin-left: 0;
}

.home .slider .progress-of-slides .progress-btn.active {
  background: #ffffff;
  color: #ffffff;
  border-color: #ffffff;
}

@media (min-width: 640px) {
  .home .slider .progress-of-slides .progress-btn {
    width: 25%;
    padding: 10px 0;
    border-top: 1px solid #ffffff;
    transition: border-color .4s linear, color .4s linear;
    height: auto;
    background: transparent;
    border-radius: 0;
    margin: 0;
  }
  .home .slider .progress-of-slides .progress-btn.active {
    background: transparent;
  }
}

.home .block-1 .item a {
  position: relative;
  display: block;
  overflow: hidden;
  background-color: #212121;
  text-align: center;
  outline: none;
  text-decoration: none;
  transition: color 0.2s ease-in-out, background-color 0.2s ease-in-out;
}

.home .block-1 .item a:before {
  content: '';
  position: absolute;
  top: 10px;
  left: 10px;
  right: 10px;
  bottom: 10px;
  border: 1px solid rgba(255, 255, 255, 0.25);
}

.home .block-1 .item a:active, .home .block-1 .item a:focus, .home .block-1 .item a:hover {
  color: #99ccff;
}

.home .block-1 .item a:active img, .home .block-1 .item a:focus img, .home .block-1 .item a:hover img {
  opacity: 0.25;
  transform: translate3d(0, 0, 0);
}

.home .block-1 .item a:active span, .home .block-1 .item a:focus span, .home .block-1 .item a:hover span {
  color: #99ccff;
  transform: translate3d(0, 0, 0);
}

.home .block-1 .item a:active p, .home .block-1 .item a:focus p, .home .block-1 .item a:hover p {
  opacity: 1;
  transform: translate3d(0, 0, 0);
}

.home .block-1 .item a:active .overlay-border:before,
.home .block-1 .item a:active .overlay-border:after, .home .block-1 .item a:focus .overlay-border:before,
.home .block-1 .item a:focus .overlay-border:after, .home .block-1 .item a:hover .overlay-border:before,
.home .block-1 .item a:hover .overlay-border:after {
  transform: scale(1, 1);
  border-color: #99ccff;
}

.home .block-1 .item img {
  display: block;
  width: calc(100% + 30px);
  max-width: none;
  height: 100%;
  opacity: 0.6;
  transform-style: preserve-3d;
  transform: translate3d(-30px, 0px, 0px);
  transition: opacity 0.35s ease 0s, transform 0.35s ease 0s;
}

.home .block-1 .item .item-description {
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  transform: translateY(-50%);
}

.home .block-1 .item .item-description span {
  display: block;
  color: #fff;
  text-transform: uppercase;
  transition: color 0.2s ease-in-out;
}

.home .block-1 .item .item-description p {
  margin: 0;
  color: rgba(255, 255, 255, 0.5);
  opacity: 0;
  transform: translate3d(-30px, 0px, 0px);
  transition: opacity 0.35s ease 0s, transform 0.35s ease 0s;
}

.home .block-1 .item .overlay-border {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.home .block-1 .item .overlay-border:before, .home .block-1 .item .overlay-border:after {
  content: '';
  position: absolute;
  top: 10px;
  right: 10px;
  bottom: 10px;
  left: 10px;
  z-index: 3;
  display: block;
  transition: transform 0.35s ease 0s;
}

.home .block-1 .item .overlay-border:before {
  border-top: 1px solid #fff;
  border-bottom: 1px solid #fff;
  transform: scale(0, 1);
}

.home .block-1 .item .overlay-border:after {
  border-left: 1px solid #fff;
  border-right: 1px solid #fff;
  transform: scale(1, 0);
}
